/*
  ANIMATION TYPE: FADE UP SLOW
*/
@keyframes fade-up-slow {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  30% {
    opacity: 1;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Hide element before animation */
[aa-animate="fade-up-slow"] {
  opacity: 0;
}

/* When in view, play animation */
[aa-animate="fade-up-slow"].in-view {
  animation: fade-up-slow var(--animation-duration, 1s) ease-out forwards;
  animation-delay: var(--animation-delay, 0s);
}

/*
  ANIMATION TYPE: FADE DOWN SLOW
*/
