/*
  ANIMATION TYPE: ABSOLUTE DIV SLIDE DOWN
*/
@keyframes slide-down {
  0% {
    height: 0%;
  }
  100% {
    height: 100%;
  }
}

[aa-animate="slide-down"] {
  height: 0%;
}

[aa-animate="slide-down"].in-view {
  animation: slide-down var(--animation-duration, 0.6s) cubic-bezier(0.4, 0, 0.25, 1) forwards;
  animation-delay: var(--animation-delay, 0s);
}

section[aa-animate="bg-swipe-in"] {
  position: relative;
  width: 100%;
  height: 100vh; /* Adjust this height as needed */
  overflow: hidden;
}

/*
  ANIMATION TYPE: PSEUDO BACKGROUND SLIDE DOWN
*/
[aa-animate="bg-slide-down"] {
  position: relative;
  background: var(--background-color--background-page);
}

[aa-animate="bg-slide-down"]::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  background: var(--background-color--background-alternate);
  z-index: 0;
  transition: height var(--animation-duration, 0.6s) cubic-bezier(0.4, 0, 0.25, 1);
  transition-delay: var(--animation-delay, 0.2s);
}

[aa-animate="bg-slide-down"].in-view::before {
  height: 100%;
}